/** @format */
.mobile-view-navbar {
  position: sticky;
  top: 0;
  z-index: 1;
}
.navbar-header {
  width: 100%;
  background: linear-gradient(#00e5ff, #5393ece0);
  color: white;
  padding: 5px;
}
#dark .navbar-header {
  background: linear-gradient(#03393f, #021f47e0);
}
.navbar-header p svg {
  margin-left: 10px;
  cursor: pointer;
  margin-top: 10px;
}
#dark .navbar-header p svg {
  color: rgb(13, 58, 240);
}
.mobile-nav {
  height: auto;
  width: 210px;
  background: linear-gradient(#00e5ff, #5393ece0);

  margin-top: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  z-index: 1;
}
#dark .mobile-nav {
  background: linear-gradient(#03393f, #021f47e0);
}
.mobile-nav ul li {
  color: black;
  font-size: 17px;
  font-weight: 600;
  list-style-type: none;
  cursor: pointer;
  text-decoration: none;
  margin-top: 30px;
}
#dark .mobile-nav ul li {
  color: white;
}
.mobile-nav ul li a {
  text-decoration: none;
  color: white;
}
.nav-item-mobile-view .active {
  color: black;
  text-decoration: none;
  background-color: blueviolet;
  border-radius: 90px;
  padding: 5px;
  width: 100%;
}
@media (min-width: 1000px) {
  .mobile-view-navbar {
    display: none;
  }
}
