/** @format */

.sidebar-section {
  display: flex;
  background: linear-gradient(to left, transparent, #5393ecde);

  /* height: 55vh; */
}
#dark .sidebar-section {
  background: linear-gradient(to left, transparent, #032555de);
}
.sidebar {
  height: 100vh;
  background: linear-gradient(#07353a6b, #05204677);
  width: 95px;
}
#dark .sidebar-section {
  background-color: #02102bda;
}

.icon-for-sidebar-expand-and-collapse {
  display: flex;
  align-items: right;
  justify-content: right;
  cursor: pointer;
}
.icon-for-sidebar-expand-and-collapse p svg {
  background: linear-gradient(#00e5ff, #5393ece0);
  padding: 5px;
  color: white;
  border-radius: 5px;
  margin-top: 40px;
}
#dark .icon-for-sidebar-expand-and-collapse p svg {
  background: linear-gradient(#1808ab, #186bd0e0);
}
.container-fluid {
  /* position: fixed; */
  padding-left: 0px !important;
  padding-right: 0px !important;
  overflow: hidden;
}
.sidebar-expand {
  width: 230px;
}
@media (max-width: 1000px) {
  .sidebar {
    display: none;
  }
}
