/** @format */
.about-section {
  box-shadow: 10px 10px 5px 0px rgba(246, 242, 242, 0.75);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(240, 224, 224, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(217, 209, 209, 0.75);
  margin-top: 50px;
  height: 70vh;
  border-radius: 8px;
  margin-bottom: 20px;
}
#dark .about-section {
  box-shadow: 11px 10px 8px -4px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 11px 10px 8px -4px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 11px 10px 8px -4px rgba(0, 0, 0, 0.75);
}
.juu {
  /* background-color: red; */
  position: relative;
}
.back-about {
  background: linear-gradient(#00e5ff, #5393ece0);
  width: 41.5%;
  height: 60%;
  position: absolute;
  border-radius: 71% 29% 81% 19% / 71% 57% 43% 29%;
  top: 23%;
  left: 37%;
  z-index: -1;
}
#dark .back-about {
  background: linear-gradient(#03393f, #021f47e0);
}
.years-of-experience {
  background-color: rgba(194, 223, 230, 0.809);
  backdrop-filter: blur(4px);
  width: 20%;
  height: 20%;
  position: absolute;
  border-radius: 71% 29% 81% 19% / 71% 57% 43% 29%;
  top: 18%;
  left: 35%;
  z-index: -1;
  text-align: center;
}
#dark .years-of-experience,
#dark .projects-completed,
#dark .happy-clients {
  background: rgba(3, 48, 125, 0.59);
}
.projects-completed {
  background-color: rgba(194, 223, 230, 0.809);
  backdrop-filter: blur(4px);
  width: 20%;
  height: 20%;
  position: absolute;
  border-radius: 71% 29% 81% 19% / 71% 57% 43% 29%;
  top: 62%;
  left: 30%;

  text-align: center;
  line-height: 10px;
}
#para {
  z-index: -1;
}
.happy-clients {
  background-color: rgba(194, 223, 230, 0.809);
  backdrop-filter: blur(4px);
  width: 20%;
  height: 20%;
  position: absolute;
  border-radius: 71% 29% 81% 19% / 71% 57% 43% 29%;
  top: 60%;
  left: 65%;
  text-align: center;
  line-height: 10px;
}
.juu h5 {
  font-weight: 600;
  color: #0088ff;
  margin-top: 10px;
}
#dark .juu h5 {
  color: rgb(28, 139, 243);
}
.about-img img {
  height: 340px;
  width: 340px;
  border-radius: 75% 25% 50% 50% / 34% 23% 77% 66%;
  /* border: 6px solid #137dae; */
  margin: 50px;
  margin-top: 50px;
  display: flex;
  vertical-align: middle;
  object-fit: cover;
  margin-left: 150px;
  transform: rotate(-21deg);
  line-height: 10px;
}

.about-details {
  margin-top: 50px;
}
.about-details p {
  line-height: 28px;
  font-weight: 600;
  font-size: 17px;
}
.abouot-title {
  text-align: center;
}
.line {
  display: block;
  width: 70px;
  height: 3px;
  margin: 1px auto;
  background-color: #012b3e;
}
#dark .line {
  background-color: #137dae;
}
@media (max-width: 1000px) {
  .about-section {
    height: auto;
  }
  .about-img img {
    margin: auto;

    margin-left: 0;
    margin-bottom: 18px;
  }
  .back-about {
    width: 70%;
    height: 60%;

    border-radius: 71% 29% 81% 19% / 71% 57% 43% 29%;
    top: 23%;
    left: 18%;
  }
  .years-of-experience {
    width: 26%;
    height: 26%;

    top: 13%;
    left: 20%;
    z-index: -1;
    text-align: center;
  }
  .projects-completed {
    width: 27%;
    height: 27%;

    top: 62%;
    left: 7%;

    text-align: center;
  }
  .happy-clients {
    width: 26%;
    height: 26%;

    top: 56%;
    left: 70%;
    text-align: center;
  }
  .juu {
    width: 100%;
  }
}
