/** @format */
.techStackSection {
  margin-top: 40px;
  height: auto;
  margin-bottom: 40px;
}
.sectionTitle h5 {
  text-align: center;
}
.tech-content {
  padding: 10px;
  color: black;
  margin-top: 50px;
  border-radius: 10px;
  background: linear-gradient(#353a5f4a, #9ebaf353);
  backdrop-filter: blur(3px);
  box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.75);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.75);
}
#dark .tech-content {
  background: linear-gradient(#1b1e344a, #1f4aa253);
  box-shadow: 11px 10px 8px -4px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 11px 10px 8px -4px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 11px 10px 8px -4px rgba(0, 0, 0, 0.75);
}
#dark .tech-content:hover {
  background: linear-gradient(#505aa94a, #386ace53);
  box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.35);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.415);
  -moz-box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.422);
}
.techContentMarked {
  background-color: #137dae;
  color: white;
}
.tech-content p {
  text-align: center;
  padding-top: 30px;
  font-size: 19px;
  font-weight: 700;
  color: black;
}
#dark .tech-content p {
  color: white;
}
.tech-content:hover {
  background-color: #137dae;
  color: white;
  cursor: pointer;
  transition-duration: 1s;
  box-shadow: 10px 10px 5px 0px rgba(24, 21, 21, 0.75);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(23, 21, 21, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(29, 22, 22, 0.75);
}
.techNumber {
  border-radius: 6px;
  height: 200px;
  width: 350px;
  text-align: center;
  justify-content: center;
  display: block;
  margin: 0 auto;
  margin-top: -35px;
  line-height: 50px;
}
.load-more-techStack {
  background-color: #057ded;
  width: 150px;
  padding: 10px;
  color: white;
  text-align: center;
  justify-content: center;
  cursor: pointer;
  display: table;
  margin-left: auto;
  margin-right: auto;
  margin-top: 35px;
  border-radius: 5px;
  margin-bottom: 40px;
}
#dark .load-more-techStack {
  background-color: #03213c;
}
#dark .load-more-techStack:hover {
  background-color: #0d4d84;
}
.load-more-techStack:hover {
  background-color: #8db9e4;
  transition-duration: 0.5s;
  color: black;
}
.center-image-port {
  height: 200px;
  width: 350px;
  display: block;
  margin: 0 auto;
  object-fit: cover;
  margin-top: -30px;
  justify-content: center;
  align-items: center;
  border-radius: 7px;
  color: white;
}
