/** @format */

.contact-section {
  height: 650px;
  margin-bottom: 50px;
  margin-left: 120px;
  border-radius: 10px;
  box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.75);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.75);
}
#dark .contact-section {
  box-shadow: 11px 12px 37px -12px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 11px 12px 37px -12px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 11px 12px 37px -12px rgba(0, 0, 0, 0.75);
}
.contact-form-image img {
  height: 600px;
  width: 100%;
  border-radius: 10px;
  object-fit: cover;
}
.contact-form input {
  height: 50px;
  border-radius: 10px;
  margin-top: 10px;
  width: 100%;
  margin-bottom: 20px;
}
#dark .contact-form input {
  background-color: #222;
  border: 2px solid darkblue;
}
.custom-select-tag {
  height: 50px;
  border-radius: 10px;
  margin-top: 10px;
  width: 100%;
  margin-bottom: 20px;
}
#dark .custom-select-tag {
  background-color: #222;
  border: 2px solid darkblue;
}
.contact-form textarea {
  border-radius: 0px;
  margin-top: 10px;
  width: 100%;
  margin-bottom: 20px;
}
#dark .contact-form #testy {
  background-color: #222;
  border: 2px solid darkblue;
}
.button-submit p {
  background-color: black;
  padding: 10px;
  border-radius: 30px;
  color: yellowgreen;
  width: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-top: 40px;
  margin-bottom: 20px;
}
.button-submit p:hover {
  background-color: brown;
  transition-duration: 1s;
}
@media (max-width: 1000px) {
  .contact-section {
    margin-left: 1px;
  }
  .contact-form-image img {
    height: 300px;
  }
}
