/** @format */

.projectList {
  margin: 10px;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.75);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(227, 227, 227, 0.75);
  background: linear-gradient(#353a5f4a, #9ebaf353);
  backdrop-filter: blur(3px);
}
#dark .projectList {
  background: linear-gradient(#1b1e344a, #1f4aa253);
  box-shadow: 11px 10px 8px -4px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 11px 10px 8px -4px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 11px 10px 8px -4px rgba(0, 0, 0, 0.75);
}
.projectList:hover {
  border-left: 5px solid #137dae;
  cursor: pointer;
  transition-duration: 1s;
}
.titleAndCollapseOption {
  display: flex;
  justify-content: space-between;
  margin: 5px;
}
.techUsedInProject {
  margin-top: 20px;
  margin: 5px;
}
.developer-skills {
  width: 130px;
  height: 130px;

  position: relative;
}
.techUsedInProject p {
  /* background-color: #137dae; */

  border-radius: 90px;
  padding: 8px;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
}
