#number {
  font-weight: 600;
  color: #061d68;
}
#dark .techUsedInProject p {
  background-color: #03213c;
}
#dark .techUsedInProject p:hover {
  background-color: #06467e;
}
.live-demo-button {
  background-color: rgb(7, 36, 122);
  border-radius: 90px;
  color: white;
  width: fit-content;
  margin: 5px;
  padding: 10px;
}
#dark .live-demo-button {
  background-color: #064f68;
}
#dark .live-demo-button:hover {
  background-color: #061d68;
}
.live-demo-button:hover {
  background-color: #137dae;
  transition-duration: 1s;
}
.live-demo-button a {
  color: white;
  text-decoration: none;
  padding: 10px;
}
.techUsedInProject p:hover {
  background-color: rgb(0, 0, 0);
}
.project-list-open {
  border-left: 5px solid blueviolet;
}
.description {
  margin: 5px;
  font-family: Georgia, "Times New Roman", Times, serif;
}
