/** @format */

.sidebar-profile-pic img {
  border-radius: 90px;
  margin-top: 10px;
  height: 145px;
  width: 145px;
  display: flex;
  vertical-align: middle;
  margin: 0 auto;
  object-fit: cover;
  border: 3px solid #2d27ff;
}
.navbar-items ul li {
  color: white;
  list-style-type: none;
  cursor: pointer;
  margin-top: 20px;
  font-weight: 700;
}
.navbar-items ul li svg {
  margin-left: 10px;
  margin-right: 25px;
}
.navbar-items-only-icons ul li {
  margin-top: 30px;
  list-style-type: none;
  cursor: pointer;
}
.navbar-items-only-icons ul li svg {
  margin-left: 10px;
}
.nav-item .active {
  border-left: 3px solid #2d27ff;
  color: #2d27ff;
}
.navbar-items ul li a {
  text-decoration: none;
}
