/** @format */

.testimonialSection {
  margin-top: 70px;
  height: 65vh;
  width: 90%;
  margin-left: 109px;
}
.contentSliderMain {
  margin-top: 60px;
}
.contentSlider {
  padding: 10px;
  color: white;
  background: linear-gradient(#00e5ff, #5393ece0);
  margin-top: 50px;
  text-align: center;
  margin-bottom: 30px;
  cursor: pointer;
  height: 210px;
  margin: 5px;
  border-radius: 5px;
  font-family: "Space Grotesk", sans-serif;
}
#dark .contentSlider {
  background: linear-gradient(#02474f, #05295de0);
}
#dark .contentSlider:hover {
  background: linear-gradient(#177b87, #0e4593e0);
}
.contentSlider:hover {
  background: linear-gradient(#023036, #05367be0);
  transition-duration: 0.5s;
}
.contentSlider img.center-image {
  height: 80px;
  width: 80px;
  border-radius: 90px;
  display: block;
  margin: 0 auto;
  object-fit: cover;
  margin-top: -30px;
  justify-content: center;
  align-items: center;
  border: 5px solid #1200ff;
  color: white;
}
#dark .contentSlider img.center-image {
  border: 5px solid #033e68;
}
.slick-prev::before {
  color: #1200ff !important;
  font-size: 10px;
}
.slick-next::before {
  color: #1200ff !important;
  font-size: 10px;
}
@media (max-width: 1000px) {
  .testimonialSection {
    margin-left: 1px;
    height: 50vh;
  }
  .contentSlider {
    padding: 1px;
    margin-bottom: 10px;
  }
}
