/** @format */
@import url("https://fonts.googleapis.com/css2?family=Passions+Conflict&display=swap");

.home {
  height: 100vh;
  color: white;
  margin-left: 107px;

  font-family: "Space Grotesk", sans-serif;
  /* display: flex; */
}
#dark .home {
  color: rgb(12, 124, 229);
  font-weight: 700;
}
.home-content {
  /* line-height: 400px; */

  padding: 10px;
  /* margin-top: 100px; */
  width: 100%;
  height: 100%;
  display: flex;

  /* background-color: red; */

  /* margin-left: 60px; */
}
.home-content h2 {
  margin: auto;
  margin-top: 20px;
  color: rgb(8, 20, 63);
  font-weight: 700;
}
#dark .home-content h2 {
  color: antiquewhite;
}
.button-for-action {
  display: flex;
}
.hire-me-button {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  text-decoration: none;
  color: #333333;
  font-size: 18px;
  border-radius: 7px;
  width: 160px;
  height: 40px;
  font-weight: bold;
  border: 2px solid #333333;
  transition: 0.3s;
  box-shadow: 5px 5px 0px 0px rgba(51, 51, 51, 1);
  background-color: #ffffff;
  cursor: pointer;
  margin-top: 20px;
  margin-right: 10px;
  background: linear-gradient(#00e5ff, #5393ece0);
  /* margin-left: 10px; */
}
#dark .hire-me-button {
  background-color: #0d0745;
  color: rgb(160, 156, 156);
}
.hire-me-button:hover {
  box-shadow: 0 0 #333;
  color: #fff;
  background-color: #333;
}
.get-resume-button {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  text-decoration: none;
  color: #333333;
  font-size: 18px;
  border-radius: 7px;
  width: 160px;
  height: 40px;
  font-weight: bold;
  border: 2px solid #333333;
  transition: 0.3s;
  box-shadow: 5px 5px 0px 0px rgba(51, 51, 51, 1);
  background-color: #ffffff;
  cursor: pointer;
  cursor: pointer;
  margin-top: 20px;
  margin-left: 10px;
  background: linear-gradient(#00e5ff, #5393ece0);
}
.het-resume-button a {
  text-align: center;
}
#dark .get-resume-button {
  background-color: #051a2d;
}
.get-resume-button:hover {
  box-shadow: 0 0 #333;
  color: #fff;
  background-color: #333;
}
.get-resume-button a {
  text-decoration: none;
  color: black;
  width: 160px;
}
#dark .get-resume-button a {
  color: rgb(160, 156, 156);
}
.get-resume-button a:hover {
  color: black;
}
.theme-change {
  justify-content: center;
  height: 50px;
  width: 50px;
  right: 0;
  background: linear-gradient(#00e5ff, #5393ece0);
  margin-right: 10px;
  text-align: center;
  vertical-align: middle;
  line-height: 50px;
  position: fixed;
  top: 150;
  margin-top: 250px;
  border-radius: 5px;
  cursor: pointer;
  z-index: 1;
}
#dark .theme-change {
  background: linear-gradient(#03393f, #021f47e0);
}
.sun-theme-icon {
  animation: rotation infinite 5s linear;
}
#nyumbani {
  background: linear-gradient(#00e5ff64, #5393ec4d);
  backdrop-filter: blur(10px);
  border-top: 1px solid grey;
  border-left: 1px solid black;
  border-bottom: 1px solid grey;
  border-right: 1px solid black;
}
.funika {
  /* background-color: yellow; */
  width: 100%;
  height: 40%;
  margin-top: 10px;
}
.byHome {
  height: 100%;
}
.chini-home {
  /* background-color: rgb(248, 15, 15); */
  box-shadow: -2px 26px 34px -6px rgba(0, 0, 0, 0.7);
  -webkit-box-shadow: -2px 26px 34px -6px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: -2px 26px 34px -6px rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
}
#mtani {
  height: 100%;
  background-color: #00e5ff;
}
.yaHome {
  height: 100%;
}
.juu-home {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin-bottom: 50px;
  /* background-color: yellow; */
  height: 100%;
}
.juu-home-ndani {
  width: 100%;
}
.chini-home-nguo {
  /* background-color: wheat; */
  height: 80%;
  width: 100%;
  position: relative;
}
.img-ya-chini-home {
  background: url(../../image/me2.png);
  background-size: cover;
  background-position: center;
  height: 80%;
  width: 80%;
  position: relative;
  z-index: 1;
  border-radius: 50%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  filter: drop-shadow(8px 8px 4px #2e2d1362);
}
.gradient-ya-picha {
  height: 60px;
  width: 65%;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    #00e5ff65
  );
  border-radius: 50%;
}

.back-chini-ndani-njee {
  position: absolute;
  background-color: rgba(7, 29, 157, 0.564);
  width: 80%;
  height: 50%;
  top: 20%;
  backdrop-filter: blur(10px);

  border-radius: 71% 29% 81% 19% / 71% 57% 43% 29%;
}
.back-chini-ndani-center {
  position: absolute;
  background-color: rgba(0, 126, 128, 0.612);
  width: 80%;
  height: 50%;
  top: 30%;
  border-radius: 71% 29% 19% 81% / 30% 53% 47% 70%;
  backdrop-filter: blur(10px);
  border-right: 4px ridge rgba(96, 108, 105, 0.495);
}
.background-ya-chini-2 {
  position: absolute;
  width: 80%;
  height: 50%;
  top: 18%;
  border-radius: 71% 29% 81% 19% / 71% 57% 43% 29%;
  border: 4px double rgb(0, 255, 208);
}
.background-ya-chini-3 {
  position: absolute;
  border: 4px ridge #00e5ff;
  width: 80%;
  height: 50%;
  top: 34%;

  left: 0.5%;
  border-radius: 71% 29% 19% 81% / 30% 53% 47% 70%;
}
.back-chini-ndani-icon1 {
  /* background: linear-gradient(#00e5ff67, rgb(0, 255, 208)); */
  height: 55px;
  width: 55px;
  position: absolute;
  top: 20%;
  left: 5%;
  z-index: 2;
  border-radius: 50%;
  font-size: 25px;
  text-align: center;
  backdrop-filter: blur(10px);
}
.back-chini-ndani-icon2 {
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #00e5ff8d;
  width: 35%;
  right: 8%;
  top: 22%;
  border-radius: 10px;
  height: 8%;
  background-position: center;
  box-sizing: border-box;
  backdrop-filter: blur(3px);
  line-height: 10px;
  /* animation: loader 5s infinite;
  z-index: 2; */
}
.back-chini-ndani-achi {
  position: absolute;
  bottom: 40%;
  right: 7%;
  z-index: 2;
  text-shadow: -11px 15px 4px rgba(0, 0, 0, 0.35);
}
.back-chini-ndani-discount {
  position: absolute;
  /* background-color: #00e5ff; */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35%;
  right: 40%;
  top: 2%;
  border-radius: 10px;
  height: 9%;
  animation: loader 5s infinite;
}
.back-chini-ndani-ndani {
  background: rgba(0, 85, 255, 0.101);
  height: 10%;
  width: 70%;
  position: absolute;
  bottom: 10%;
  text-align: center;
  border-radius: 10px;
  text-shadow: -11px 15px 4px rgba(0, 0, 0, 0.35);
  backdrop-filter: blur(10px);
  border-top: 1px solid rgba(128, 128, 128, 0.365);
  border-bottom: 0.5px solid grey;
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes loader {
  0% {
    translate: 20px 0px;
    background: url("https://i.pinimg.com/originals/7f/3a/68/7f3a68fe9ae08dfcfb26a0863a20d375.gif");
    background-position: center;
    box-sizing: border-box;
    color: #fff;
    /* background: url("https://img.icons8.com/external-smashingstocks-flat-smashing-stocks/66/000000/external-Website-valentine's-day-smashingstocks-flat-smashing-stocks.png");npm  */
  }
  25% {
    translate: 20px 20px;
    background-color: #051a2d77;
    background: url("https://i.pinimg.com/originals/7f/3a/68/7f3a68fe9ae08dfcfb26a0863a20d375.gif");
    background-position: center;
    box-sizing: border-box;
    color: #fff;
  }
  50% {
    translate: 20px 20px;
    background: url("https://i.pinimg.com/originals/7f/3a/68/7f3a68fe9ae08dfcfb26a0863a20d375.gif");
    background-position: center;
    box-sizing: border-box;
    rotate: -0deg;
    color: #fff;
    scale: 2;
  }
  75% {
    translate: 20px 0px;
    background: url("https://i.pinimg.com/originals/7f/3a/68/7f3a68fe9ae08dfcfb26a0863a20d375.gif");
    background-position: center;
    box-sizing: border-box;
    rotate: -0deg;
    color: #fff;
    scale: 1;
  }
  100% {
    translate: 20px 0px;
    rotate: -0deg;
    background: url("https://i.pinimg.com/originals/7f/3a/68/7f3a68fe9ae08dfcfb26a0863a20d375.gif");
    background-position: center;
    box-sizing: border-box;
    color: #fff;
  }
}
.picha-ya-hapa {
  position: absolute;
  top: -10%;
  left: -15%;
  animation: loader1 2.2s infinite;
}
@keyframes loader1 {
  0% {
    rotate: 0deg;
  }
  50% {
    rotate: 180deg;
  }
  100% {
    border-radius: 50%;
    rotate: 360deg;
  }
}
.icon-ndani-kabisa-text h5,
.icon-ndani-kabisa-text h6 {
  font-size: 20px;
  color: red;
}
.icon-ndani-kabisa-texts {
  width: 60%;
}
@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (max-width: 1000px) {
  .home {
    margin-left: 1px;
    height: 115vh;
  }
  .juu-home {
    height: auto;
    padding: 30px;
  }
  .hire-me-button {
    width: 150px;
    height: 50px;
  }
  .get-resume-button {
    width: 150px;
    height: 50px;
  }
  .chini-home {
    /* background-color: rgb(248, 15, 15); */

    height: 75%;
    position: relative;
    display: flex;
    justify-content: center;
  }
}
@media (max-width: 1000px) {
  .theme-change {
    display: none;
  }
  .img-ya-chini-home {
    margin-left: px;
  }
}
